@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.crypto-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: rgb(155, 13, 13);
}

.crypto-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.crypto-text {
  margin-bottom: 32px;
  text-align: center;
  color: rgb(17, 226, 17);
}

.crypto-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #1320d8 0%,
    #18f23c 48%,
    #01eeff 100%
  );

  color: #ffffff;
}

.crypto-input::placeholder {
  color: #e2e2e2;
}

.crypto-input::placeholder {
  color: #020202;
  opacity: 1;
}