@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.crypto-app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 64px;
  color: rgb(155, 13, 13);
}

.crypto-search {
  margin-bottom: 64px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
}

.crypto-text {
  margin-bottom: 32px;
  text-align: center;
  color: rgb(17, 226, 17);
}

.crypto-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #1320d8 0%,
    #18f23c 48%,
    #01eeff 100%
  );

  color: #ffffff;
}

.crypto-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.crypto-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.crypto-input::placeholder {
  color: #e2e2e2;
}

.crypto-input::-webkit-input-placeholder {
  color: #020202;
  opacity: 1;
}

.crypto-input:-ms-input-placeholder {
  color: #020202;
  opacity: 1;
}

.crypto-input::placeholder {
  color: #020202;
  opacity: 1;
}
.crypto-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
  
.crypto-row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}
  
.crypto {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding-right: 24px;
  
    min-width: 300px;
}
    
.crypto h1 {
    font-size: 16px;
    width: 150px;
}
  
.crypto img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}


.crypto-symbol {
    text-transform: uppercase;
}
  
.crypto-data {
    display: -webkit-flex;
    display: flex;
    text-align: right;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 100%;
}
  
.crypto-price {
    width: 110px;
}
  
.crypto-volume {
    width: 155px;
}
  
.crypto-marketcap {
    width: 230px;
}
  
.crypto-percent {
    width: 100px;
}
  
.red {
    color: #f00606;
}
  
.green {
    color: #11d811;
}
